body{
    margin: 0;
}
.App {
    font-family: 'Hammersmith One', sans-serif;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    color: black;
    background-image: -webkit-gradient(linear, left bottom, left top, from(#f3e7e9), color-stop(99%, #e3eeff), to(#e3eeff));
    background-image: -webkit-linear-gradient(bottom, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);
    background-image: -o-linear-gradient(bottom, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);
    background-image: linear-gradient(to top, #f3e7e9 0%, #e3eeff 99%, #e3eeff 100%);
}

.App__Top-Bar{
    font-size: 24px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding: 10px;
}

.App__Title{
    margin-right: auto;
}

.App__Prices{
    margin-left: auto;
}

.Price__Increased{
    color: #27ae60;
}
.Price__Decreased{
    color: #c0392b;
}


